.box{
    
        background-color: #a892ee;
        padding: 1rem;
        margin: 2rem auto;
        width: 50rem;
        max-width: 95%;
        border-radius: 12px;
        text-align: center;
        box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
      
} 

.btn{
    font: inherit;
  cursor: pointer;
  padding: 1rem 2rem;
  border: 1px solid #40005d;
  background-color: #40005d;
  color: white;
  border-radius: 12px;
  margin-left: 1rem;
}